/**
 * SignalM - Lightweight real-time for modern browsers
 *
 * Compatible with ASP.NET Core SignalR hubs
 * WebSocket + JSON only, ~2-3 KB gzipped
 *
 * @packageDocumentation
 */

// Export types
export { ConnectionState } from './types';
export type {
  SignalMOptions,
  Message,
  InvocationMessage,
  CompletionMessage,
  PingMessage,
  CloseMessage,
  MessageType,
  ConnectionEvent
} from './types';

// Export retry policies
export {
  ExponentialBackoffRetryPolicy,
  FixedRetryPolicy,
  NoRetryPolicy,
  CustomRetryPolicy
} from './RetryPolicy';
export type { IRetryPolicy } from './RetryPolicy';

// Export protocol
export { JsonProtocol } from './JsonProtocol';

// Export event emitter
export { EventEmitter } from './EventEmitter';

// Version
export const VERSION = '0.1.0';

// Note: SignalMConnection will be exported here once implemented
// export { SignalMConnection } from './SignalMConnection';
